.Intro{
    background-image: url('../../assets/school_intro.png');
}
.Work{
    background-image: url('../../assets/school_workplace.png');
}
.Background{
    height: 100vh;
    background-size: cover;
}

.Background-enter {
    opacity: 0.3;
}
.Background-enter-active {
    opacity: 0.3;
    transition: opacity 100ms;
}
.Background-enter-done {
    opacity: 1;
    transition: opacity 100ms;
}

.Background-exit-active {
    opacity: 0.3;
    transition: opacity 100ms;
}
.Background-exit-done {
    opacity: 0.3;
    transition: opacity 100ms;
}

@media(max-width: 1000px){
    .Background{
        height: 100% !important;
    }
}
